                      <th 
                        v-for="(candidatura, index) in votosXVA.datos_nombres_columnas"
                          :key="index">
                          <img v-if="candidatura.candidatura_logo != 'sin logo' " style="width: 30px;"
                          :src="candidatura.candidatura_logo" :alt="candidatura.candidatura_nombre" />
                          
