                                  :icon="['far', 'file']"
                                  class="mr-2 conUrl">
                            </font-awesome-icon>
                            
                          </a>  
                        </td>
                        <td v-else>
                          <font-awesome-icon
                          icon="hourglass"
                          class=" mr-2 sinUrl"
                          ></font-awesome-icon>
                          <font-awesome-icon
                                  :icon="['far', 'file']"
