                        >                      
                        {{ candidatura.votos }}                         
                        </td>

                        <td  >
                        {{ seccion.candidatura_no_registrada }}
                        </td>

                        <td >
                        {{ seccion.votos_nulos }}
                        </td>

                        <td>
                        {{ seccion.votos_total }}
