    border-radius: 6px;
    padding: 5px 0;
    // margin: 5px;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;

    font-size: smaller;

    bottom: 100%;
    left: 50%;
    margin-left: -125px;
    opacity: 0.85;

    margin-bottom: 5px;

    &::after {
      content: " ";
      position: absolute;
      top: 100%; /* At the bottom of the tooltip */
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: black transparent transparent transparent;
    }
  }

  &:hover{
    .tooltiptext{
      visibility: visible;
    }
  }
}
